<template>
  <div class="container">
    <img src="../assets/rules.png">
    <div class="info">
      
      <h2>{{ $t("secret.computerchoice") }} : <span id="computer-choice"></span></h2>
      <h2>{{ $t("secret.userchoice") }} : <span id="user-choice"></span></h2>
      <h2>&nbsp;<span id="result"></span></h2>
    </div>

    <div class="button-container">
      <button v-on:click="userChoice($event)" value="üëä">üëä {{ $t("secret.choices.rock") }} üëä</button>
      <button v-on:click="userChoice($event)" value="‚úã">‚úã {{ $t("secret.choices.paper") }} ‚úã</button>
      <button v-on:click="userChoice($event)" value="‚úåÔ∏è">‚úåÔ∏è {{ $t("secret.choices.scissors") }} ‚úåÔ∏è</button>
      <button v-on:click="userChoice($event)" value="ü§è">ü§è {{ $t("secret.choices.lizard") }} ü§è</button>
      <button v-on:click="userChoice($event)" value="üññ">üññ {{ $t("secret.choices.spock") }} üññ</button>
    </div>
  </div>
</template>

<script>

let userChoice;
let computerChoice;
let result;

export default {
  methods: {
    userChoice(e) {
      const userChoiceDisplay = document.getElementById('user-choice')
      userChoice = e.target.value;
      userChoiceDisplay.textContent = userChoice;
      this.generateComputerChoice();
      this.getResult();
      return userChoice = ''
    },

    randomRange(min, max) {
      min = Math.ceil(min);
      max = Math.floor(max);
      return Math.floor(Math.random() * (max - min + 1) + min);
    },

    generateComputerChoice() {
      const computerChoiceDisplay = document.getElementById('computer-choice')
      const randomNumber = this.randomRange(1, 5);

      switch (randomNumber) {
        case 1: computerChoice = 'üëä';
        break;
        case 2: computerChoice = '‚úã';
        break;
        case 3: computerChoice = '‚úåÔ∏è';
        break;
        case 4: computerChoice = 'ü§è';
        break;
        case 5: computerChoice = 'üññ';
        break;
      }
      computerChoiceDisplay.textContent = computerChoice;
    },

    getResult() {
      const resultDisplay = document.getElementById('result')

      if (computerChoice === userChoice) {
        result = 'üëâüëà'
      }
      if (computerChoice === '‚úã' && (userChoice === '‚úåÔ∏è' || userChoice === 'ü§è')) {
        result = 'üëç'
      }
      if (computerChoice === '‚úã' && (userChoice === 'üëä' || userChoice === 'üññ')) {
        result =  'üëé'
      }
      if (computerChoice === '‚úåÔ∏è' && (userChoice === '‚úã' || userChoice === 'ü§è')) {
        result =  'üëé'
      }
      if (computerChoice === '‚úåÔ∏è' && (userChoice === 'üëä' || userChoice === 'üññ')) {
        result = 'üëç'
      }
      if (computerChoice === 'üëä' && (userChoice === '‚úåÔ∏è' || userChoice === 'ü§è')) {
        result =  'üëé'
      }
      if (computerChoice === 'üëä' && (userChoice === '‚úã' || userChoice === 'üññ')) {
        result = 'üëç'
      }
      if (computerChoice === 'ü§è' && (userChoice === 'üëä' || userChoice === '‚úåÔ∏è')) {
        result = 'üëç'
      }
      if (computerChoice === 'ü§è' && (userChoice === '‚úã' || userChoice === 'üññ')) {
        result =  'üëé'
      }
      if (computerChoice === 'üññ' && (userChoice === 'ü§è' || userChoice === '‚úã')) {
        result = 'üëç'
      }
      if (computerChoice === 'üññ' && (userChoice === 'üëä' || userChoice === '‚úåÔ∏è')) {
        result =  'üëé'
      }

      resultDisplay.textContent = result;
    }
    
  }
}

</script>

